<template>
  <div class="scroll">
    <div class="scroll__inner"
      :style="{ top: `${ scrollPercentage }%` }"
    ></div>
  </div>
</template>

<script setup>

  import { ref, onMounted } from 'vue'
  
  const scrollPercentage = ref( 0 )
  // const quantityPages = 4
  // const currentPage = 0

  const getScrollTopPercentage = () => {
    const scrollTop = window.pageYOffset || document.documentElement.scrollTop; 
    const scrollHeight = document.documentElement.scrollHeight - document.documentElement.clientHeight; // Высота страницы без видимой части

    return scrollTop / scrollHeight * 100; // Возвращаем процент скролла
  }

  onMounted(() => {
    window.addEventListener('scroll', () => {
      scrollPercentage.value = getScrollTopPercentage();
    });
  })
  
</script>