<template>
  <canvas id="cnv" ref="experience"></canvas>
</template>

<script setup>

  import { ref, computed, watch, onMounted } from 'vue'

  import { Scene, PerspectiveCamera, WebGLRenderer, Fog, Color, AmbientLight } from 'three';
  import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
  import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js'
  // import { useWindowSize } from '@vueuse/core'

  const experience = ref( null )

  const bgColor = new Color('#111')
  const scene = new Scene()

  scene.fog = new Fog( bgColor, 0.1, 75)
  scene.background = bgColor

  const width = ref(0)
  const height = ref(0)

  const aspectRatio = computed( () => width.value / height.value )

  const gltfLoader = new GLTFLoader()
  
  let controls = null
  let renderer = null
  let camera = null
  

  const setCamera = () => {
    camera = new PerspectiveCamera( 75, aspectRatio.value, 0.1, 1000 )
    camera.position.set( 3, 1, 4 )

    scene.add( camera )
  }

  const updateCamera = () => {
    camera.aspect = aspectRatio.value
    camera.updateProjectionMatrix()
  }


  const setLight = () => {
    const light = new AmbientLight( 0xffffff, 1.3 )
    light.position.set( 1, 4, 1 )
    scene.add( light )
  }
  

  const setModel = () => {
    gltfLoader.load('lowpoly_printer/scene.gltf', ( gltf ) => {
        const model = gltf.scene;
        model.position.y = -0.15;
        scene.add(model);
      }
    )
  }


  const setDefaultControls = () => {
    controls = new OrbitControls( camera, renderer.domElement )
    controls.enableDamping = true
    controls.enablePan = false
    controls.autoRotate = true
    controls.maxDistance = 0.5
    controls.minDistance = 0.5
  }


  const updateRenderer = () => {
    renderer.setSize( width.value, height.value )
    renderer.render( scene, camera )
  }

  const setRenderer = () => {
    if( experience.value ) {
      renderer = new WebGLRenderer({ canvas: experience.value, alpha: true })
      renderer.setPixelRatio( Math.min( window.devicePixelRatio, 2 ) )

      setDefaultControls()

      updateRenderer()
    }
  }


  const loop = () => {
    controls.update()
    renderer.render( scene, camera )
    requestAnimationFrame( loop )
  } 


  watch( aspectRatio, () => {
    updateCamera()
    updateRenderer()
  })


  const reSizeCnv = () => {
    width.value = experience.value.offsetWidth
    height.value = experience.value.offsetHeight
  }


  onMounted(() => {
    window.onresize = reSizeCnv()

    setCamera()
    setLight()
    setModel()
    setRenderer()

    loop()
  })

</script>