<script setup>
  import vModelViewer from '@/components/canvas/vModelViewer.vue';
  import vCarousel from '@/components/carousel/vCarousel.vue';
  import vScroll from '@/components/background/vScroll.vue';
  import vLine from '@/components/background/vLine.vue';

  import { onMounted, nextTick } from 'vue';

  const cards = [
    {
      img: 'camera',
      title: 'Камеры',
      description: 'Ремонт, установка видео наблюдения в помещениях и на территории объекта',
    },
    {
      img: 'printer',
      title: 'Принтеры',
      description: 'Ремонт, обслуживание и поставка принтеров',
    },
    {
      img: 'cartridge',
      title: 'Картриджи',
      description: 'Заправка, чистка и починка оригинальных и совместимых картриджей для принтеров',
    },
    {
      img: 'signal',
      title: 'Сигнализация',
      description: 'Монтаж, обслуживание и настройка охранных систем сигнализации',
    },
    {
      img: 'computer',
      title: 'Компьютеры',
      description: 'Сборка, ремонт, модернизация персональных компьютеров',
    },
  ]

  
  onMounted(() => {
    window.scrollTo( 0, 0 )
    
    nextTick(() => {

      const sections = document.querySelectorAll('section');
      let currentSectionIndex = 0;
      let scrollTimeout = null;

      window.addEventListener('wheel', (event) => {
        if (scrollTimeout) {
          clearTimeout(scrollTimeout);
        }

        scrollTimeout = setTimeout(() => {
          if (event.deltaY < 0) {
            currentSectionIndex = (currentSectionIndex - 1 + sections.length) % sections.length;
            sections[currentSectionIndex].scrollIntoView({ behavior: 'smooth' });
          } else {
            currentSectionIndex = (currentSectionIndex + 1) % sections.length;
            sections[currentSectionIndex].scrollIntoView({ behavior: 'smooth' });
          }
        }, 100);
      });

    })
  })
</script>

