<template>
  <div class="carousel">
    <div class="carousel__inner">

      <vCarouselItem
        :style="{ transform: `translateX( calc( ( -100% - 30px ) * ${ current } ) ) scale( 0.9 )` }"
        :class="{ 'carousel-item_active': index === current + 1 }"
        v-for="( item, index ) in items" :key="index" :item="item"
      ></vCarouselItem>

      <div class="carousel__container-btn">
        <button class="carousel__btn carousel__btn_left"
          :class="{ 'carousel__btn_disabled': current == -1 }"
          @click="prev"
        ></button>
        
        <button class="carousel__btn carousel__btn_right"
          :class="{ 'carousel__btn_disabled': current === items.length - 2 }"
          @click="next"
        ></button>
      </div>
      
    </div>
  </div>
</template>

<script setup>

  import vCarouselItem from '@/components/carousel/vCarouselItem.vue'
  import { ref, defineProps } from 'vue'
  
  const props = defineProps({
    items: {
      required: true,
      type: Array,
    }
  })

  const current = ref( 0 )
  let timer = false

  const next = () => {
    if( timer ) return
    else timer = true

    current.value === props.items.length - 2
      ? current.value = props.items.length - 2
      : current.value++

    setTimeout( () => timer = false, 200 )
  }
  
  const prev = () => {
    if( timer ) return
    else timer = true

    current.value === -1
      ? current.value = -1
      : current.value--

    setTimeout( () => timer = false, 200 )
  }

</script>